function sliderServices() {
    var swiper = new Swiper('.services .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 20,
        navigation: {
            nextEl: ".services .swiper-button-next",
            prevEl: ".services .swiper-button-prev",
        },
        breakpoints: {
            320: {
                spaceBetween: 10,
            },
            993: {
                spaceBetween: 20,
            },
        }
    });
}

function planGallery() {
    var swiper = new Swiper(".plan__images .mySwiper", {
        slidesPerView: 4,
        spaceBetween: 16,
        freeMode: true,
    });
    var swiper2 = new Swiper(".plan__images .mySwiper2", {
        spaceBetween: 16,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });
}

$(document).ready(function () {
    sliderServices();
    planGallery();

    // Menu
    $('.burger').click(function () {
        $('.menu').addClass('menu--active')
        $('body').addClass('fixed-body')
    })

    $('.menu__close').click(function () {
        $('.menu').removeClass('menu--active')
        $('body').removeClass('fixed-body')
    })

    // Header
    // let lastScrollTop = 0;
    // const header = $('#header');
    //
    // $(window).scroll(function() {
    //     let scrollTop = $(this).scrollTop();
    //
    //     if (scrollTop > lastScrollTop) {
    //         header.addClass('header-fixed');
    //     } else {
    //         header.removeClass('header-fixed');
    //     }
    //     lastScrollTop = scrollTop;
    // });

    // Btn-animation
    $(".btn-animation").mouseenter(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    $(".btn-animation").mousemove(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    // Form
    let isClicked = false;
    $('.form').on('submit', function(e) {
        e.preventDefault();

        isClicked = true;
        let allFilled = true;

        $(this).find('.form-group input').each(function() {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
                allFilled = false;
            } else {
                $formGroup.removeClass('form-group--active');
            }
        });
    });
    $('.form-group input').on('input', function() {
        if (isClicked) {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
            } else {
                $formGroup.removeClass('form-group--active');
            }
        }
    });

    // Up
    var t = $(".up");

    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            t.addClass("up--active");
        } else {
            t.removeClass("up--active");
        }
    });

    t.on("click", function(e) {
        e.preventDefault();
        $("html, body").animate({
            scrollTop: 0
        }, 300);
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // Plan
    $('.path-btn').on('mouseenter', function(e) {
        e.stopPropagation();

        var $btn = $(this);
        var id = $btn.data('id');

        var rect = this.getBoundingClientRect();
        var top = rect.top + $(window).scrollTop();
        var left = rect.left + $(window).scrollLeft();

        var $card = $('.service-card').filter('[data-id="' + id + '"]');
        var cardWidth = $card.outerWidth();
        var windowWidth = $(window).width();

        if (left + cardWidth > windowWidth + $(window).scrollLeft()) {
            left = windowWidth + $(window).scrollLeft() - cardWidth - 10;
        }

        $card.css({
            'top': (top - 80) + 'px',
            'left': left + 'px',
            'display': 'block'
        });

        if ($(window).width() <= 576) {
            $('.section-bg').addClass('section-bg--active')
            $card.css({
                'transform': 'translate(0)'
            });
        }
    });

    $('.path-btn, .service-card').on('mouseleave', function() {
        var $this = $(this);
        var id = $this.data('id');

        setTimeout(function() {
            if (!$('.path-btn[data-id="' + id + '"]:hover').length && !$('.service-card[data-id="' + id + '"]:hover').length) {
                $('.service-card[data-id="' + id + '"]').hide();
            }
        }, 100);
    });

    //
    $('.plan__btn').click(function () {
        $('.plan__images').addClass('plan__images--active')
    })

    $('.plan__images-close').click(function () {
        $('.plan__images').removeClass('plan__images--active')
    })

    //
    $('.section-bg').click(function () {
        $(this).removeClass('section-bg--active')
    })

    //
    if ($('.plan').length) {
        const $plan = $('.plan');
        const $planMob = $plan.find('.plan-mob');
        const $img2 = $planMob.find('.plan__img2');
        const $scrollBtn = $planMob.find('.scroll-btn');
        const $body = $('body');

        $plan.addClass('fixed');
        $body.addClass('fixed-body');

        let step = 0;

        function handleAction() {
            if (step === 0) {
                $img2.addClass('hidden');
                step++;
            } else if (step === 1) {
                $planMob.addClass('hidden');
                $plan.removeClass('fixed');
                $body.removeClass('fixed-body');
                step++;
                $(window).off('wheel', onWheel);
                $(window).off('touchmove', onTouchMove);
                $scrollBtn.off('click', onClick);
            }
        }

        function onWheel(e) {
            if (e.originalEvent.deltaY > 0) {
                handleAction();
                e.preventDefault();
            }
        }

        function onTouchMove(e) {
            const touch = e.originalEvent.touches[0]; // Получаем первое касание
            if (touch.clientY < window.innerHeight / 2) { // Если пальцем скроллим вниз
                handleAction();
                e.preventDefault();
            }
        }

        function onClick(e) {
            handleAction();
            e.preventDefault();
        }

        $(window).on('wheel', onWheel);
        $(window).on('touchmove', onTouchMove); // Обработка жестов на мобильных устройствах
        $scrollBtn.on('click', onClick);
    }

})


